import * as React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import Alert from '@mui/material/Alert';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

export const WarningDialog: React.FC = () => {
  const [open, setOpen] = React.useState(true);

  const handleClick = () => {
    setOpen(false);
  }

  const handleClose = (event:Object, reason:String) => {
    if (reason !== 'backdropClick') {
      setOpen(false);
    }
  };


  
  return (
    <>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"Disclaimer"}
        </DialogTitle>
        <DialogContent>
          <Alert severity="info">Welcome to Viva! Please read the following before using Viva.</Alert>
          <br/>
          <DialogContentText id="alert-dialog-description">
          Viva is an open source decentralized application operating on the Vite blockchain. As with all early-stage products there are risks associated with using the protocol and users assume the full responsibility for these risks. You should not deposit any money you are not comfortable losing.
          <br/><br/>
          THE VIVA PROTOCOL AND ACCESS TO THE PROTOCOL IS PROVIDED "AS IS", AT YOUR OWN RISK, AND WITHOUT WARRANTIES OF ANY KIND.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button href="https://vivafi.org">Disagree</Button>
          <Button onClick={handleClick} autoFocus>
            Agree
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}